































import Vue from "vue";
import "boxicons";
export default Vue.extend({
  name: "App",
  data() {
    return {
      isLoading: false,
      isFullPage: true,
    };
  },
  methods: {
    openLoading() {
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
      }, 1500);
    },
  },
  mounted() {
    this.openLoading();
  },
});
